import axios from "axios";
import urls from "../url.js"
//
import getLocalTime from "../getLocalTimes.js"

const specialList={
	namespaced: true,
	state:{
		//
		pageIndex:1,
		//
		pageSize:10,
		//
		totalElements:0,
		cmsCreator:[],
		//
		editList:false,
		cmsCreatorlist:{}
	},
	mutations:{
		getPageSize(state,index){
			state.pageIndex = index
		},
		//1
		edittrue(state,row){
			state.cmsCreatorlist =row
			state.editList= true
		},
		//2
		editfalse(state){
			state.editList= false
		}
	},
	actions:{
		//
		async DISABLE(context,id) {
    		let datas= new FormData()
    		datas.append('id', id);
    		const {data} = await axios({
				method:"post",
				baseURL: `${urls.disable}`,
			    // baseURL: `${context.state.url}/works/getAllWorksList`,
			    timeout: 4000,
			    headers: {'cms-token': localStorage.token,'cms-channel' :'0'},
			    data:datas
			});
			if (data.code === 200) {
				context.dispatch("SPECIALLIST")
				console.log(data)
			}else {
				
			}
			
    		
    	},
    	//
    	async ENABLE(context,id) {
    		let datas= new FormData()
    		datas.append('id', id);
    		const {data} = await axios({
				method:"post",
				baseURL: `${urls.enable}`,
			    // baseURL: `${context.state.url}/works/getAllWorksList`,
			    timeout: 4000,
			    headers: {'cms-token': localStorage.token,'cms-channel' :'0'},
			    data:datas
			});
			if (data.code === 200) {
				console.log(data)
				context.dispatch("SPECIALLIST")
			}else {
				
			}
    		
    	},
    	//
    	async DELETE(context,id) {
    		let datas= new FormData()
    		datas.append('id', id);
    		const {data} = await axios({
				method:"post",
				baseURL: `${urls.delete}`,
			    // baseURL: `${context.state.url}/works/getAllWorksList`,
			    timeout: 4000,
			    headers: {'cms-token': localStorage.token,'cms-channel' :'0'},
			    data:datas
			});
			if (data.code === 200) {
				console.log(data)
				context.dispatch("SPECIALLIST")
			}else {
				
			}
    		
    	},
		//
		async SPECIALLIST(context) {
    		let datas= new FormData()
    		datas.append('pageIndex', context.state.pageIndex-1);
    		datas.append('pageSize', context.state.pageSize);
    		const {data} = await axios({
				method:"post",
				baseURL: `${urls.getTopicList}`,
			    // baseURL: `${context.state.url}/works/getAllWorksList`,
			    timeout: 4000,
			    headers: {'cms-token': localStorage.token,'cms-channel' :'0'},
			    data:datas
			});
    		if (data.code ===200) {
    			context.state.cmsCreator =[]
    			// console.log(data.data.content)
    			context.state.totalElements= data.data.totalElements
    			if (data.data.totalElements !== 0) {
	    			data.data.content.forEach((item) => {
	    				context.state.cmsCreator.push({
	    					//
							coverUrl:item.coverUrl,
							//id
							id:item.id,
							//
							createTime:getLocalTime(item.createTime),
							//
							title:item.title,
							//
							createuserName:item.cmsCreatorInfo === undefined ? item.appCreatorInfo === undefined ? "" : item.appCreatorInfo.userName :item.cmsCreatorInfo.userName,
							//
							cmsEditoruserName:item.cmsEditorInfo === undefined ? "" :item.cmsEditorInfo.userName,
							//
							updateTime:getLocalTime(item.updateTime),
							//
							//012
							dataState:item.dataState === 0 ? "" : item.dataState === 1? "" : "",
							//
							//01
							type:item.type === 0 ? "" : "",
	    				})
	    			});
	    		}
    		}else {
    			console.log(data)
    		}
    	}
	}
}
export default specialList


